// 引入通用table组件
import VXETable from '@/components/common/TableNoPage.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE
} from '@/utils/constantsFile';
// 导入所需接口
// import { GetList, WarningStatistics } from '@/api/equipmentmonitoring';
import { deviceExceptionGetDeviceExceptionComparedAnalyze } from '@/api/warn';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  props: {
    // 设备code
    typeValue: {
      type: Number,
      default: 0
    },
    modelValue: {
      type: Number | String,
      default: ''
    },
    startTime: {
      type: String,
      default: ''
    },
    endTime: {
      type: String,
      default: ''
    }
  },
  data() {
    return {};
  },
  computed: {

  },
  watch: {
  },
  created() {
    this.findList();
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      var searchContent = {
        conditionJson1: [
          {
            column: 'startTime',
            value: this.startTime
          },
          {
            column: 'endTime',
            value: this.endTime
          }
        ]
      };
      if (this.typeValue === 0) {
        searchContent.conditionJson1.push({
          column: 'groupId',
          operator: 1,
          ptype: 'int',
          type: 'eq',
          value: this.modelValue
        });
      } else {
        searchContent.conditionJson1.push({
          column: 'deviceCode',
          operator: 1,
          ptype: 'string',
          type: 'eq',
          value: this.modelValue
        });
      }

      try {
        const res = await deviceExceptionGetDeviceExceptionComparedAnalyze({}, searchContent);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const data = res.rows;
          const chartData = {
            deviceName: [],
            warnAllCount: [],
            warnAllTime: [],
            runTime: [],
            mtbf: [],
            mttr: []
          };
          for (const key in chartData) {
            if (['warnAllTime', 'runTime', 'mtbf', 'mttr'].includes(key)) {
              chartData[key] = data.map(item => parseInt(item[key]));
            } else {
              chartData[key] = data.map(item => item[key]);
            }
          };
          this.draw(chartData);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    draw(chartData) {
      this.myChart = this.$echarts.init(this.$refs.echartGantt);
      this.myChart.setOption({
        tooltip: {
          trigger: 'axis',
          // axisPointer: {
          //   type: 'cross',
          //   crossStyle: {
          //     color: '#999'
          //   }
          // },
          formatter: (params) => {
            const seriesNames = ['故障时间', '运行时间', 'MTBF', 'MTTR'];
            let str = params[0].name;
            params.forEach((item, index) => {
              if (seriesNames.includes(item.seriesName)) {
                str += `<br>${item.seriesName}: ${this.timeStampSeconds(item.value)}`;
              } else {
                str += `<br>${item.seriesName}: ${item.value}次`;
              }
            });
            return str;
          }
        },
        legend: {
          right: 10
        },
        grid: {
          containLabel: true,
          left: '5%',
          right: '1%'
        },
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            top: 'bottom'
          },
          {
            type: 'inside',
            filterMode: 'weakFilter'
          }
        ],
        xAxis: [
          {
            type: 'category',
            data: chartData.deviceName,
            axisPointer: {
              type: 'shadow'
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '小时',
            min: 0,
            // max: 250,
            // interval: 50,
            axisLabel: {
              formatter: (value, index) => {
                return parseInt(value / 60 / 60);
              }
            }
          },
          {
            type: 'value',
            name: '次',
            min: 0
            // max: 100,
            // interval: 5,
            // axisLabel: {
            //   formatter: '{value} %'
            // }
          }
        ],
        series: [
          {
            name: '故障时间',
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#c74746'
              }
            },
            barWidth: '30',
            data: chartData.warnAllTime
          },
          {
            name: '运行时间',
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#027df9'
              }
            },
            barWidth: '30',
            data: chartData.runTime
          },
          {
            name: 'MTBF',
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#fecf11'
              }
            },
            barWidth: '30',
            data: chartData.mtbf
          },
          {
            name: 'MTTR',
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#ff6b22'
              }
            },
            barWidth: '30',
            data: chartData.mttr
          },
          {
            name: '故障次数',
            type: 'line',
            yAxisIndex: 1,
            smooth: true,
            symbolSize: 10,
            itemStyle: {
              normal: {
                color: '#c74746'
              }
            },
            data: chartData.warnAllCount
          }
        ]
      });
    },
    timeStampSeconds(cellValue) {
      var day = parseInt(cellValue / 60 / 60 / 24);
      var hour = parseInt(cellValue / 60 / 60 % 24);
      var min = parseInt(cellValue / 60 % 60);
      var s = parseInt(cellValue % 60);
      cellValue = '';
      if (day > 0) {
        cellValue = day + '天';
      }
      if (hour > 0) {
        cellValue += hour + '小时';
      }
      if (min > 0) {
        cellValue += parseFloat(min) + '分钟';
      }
      if (s > 0) {
        cellValue += parseFloat(s) + '秒';
      }
      return cellValue;
    }
  }
};
