// 引入组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { BTN_TEXT, STATUS_CODE, TITLE } from '@/utils/constantsFile';
import detailTable from '@/views/warn/MttrAndMtbf/detailTable.vue';
import chartModule from '@/views/warn/MttrAndMtbf/chartModule.vue';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';

import SearchBox from '@/components/common/SearchBox';

// 引入通用接口
import { deviceGroupGetTreeFunc } from '@/utils/publicFunction';
import { deviceGroupGetTreeAction, deviceGetPageDeviceComplexByConditionAction } from '@/api/management';
// import { getPageDeviceByCondition } from '../../../../api/management';

export default {
  name: 'EquipmentMonitoring',
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,

      typeModel: 0,
      typeOption: [
        {
          name: '产线',
          value: 0
        },
        {
          name: '设备',
          value: 1
        }
      ],
      nowOption: [],
      nowOptionx: [],
      searchSelectList: [
        { value: null },
        { value: null }
      ],
      detailListName: ['', ''],
      detailListx: [],
      dateTime: [],
      pickerOptions: {
        shortcuts: [
          {
            text: BTN_TEXT.LAST_WEEK,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_MONTH,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: BTN_TEXT.LAST_THREE_MONTHS,
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      activeN: 0,
      selectTabs: [
        {
          type: 0,
          name: '图表'
        },
        {
          type: 1,
          name: '列表'
        }
      ]
    };
  },
  created() {
    // 调用列表查询接口
    // this.getWarnCount();

    const end = new Date();
    const start = new Date();
    start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
    this.dateTime = [
      this.$utils.toDateString(start, 'yyyy-MM-dd') + ' 00:00:00',
      this.$utils.toDateString(end, 'yyyy-MM-dd') + ' 23:59:59'
    ];

    this.getGroupTree();
  },
  methods: {
    toggleN(i) {
      this.activeN = i;
      this.search();
    },
    getValue3(e) {
      this.searchSelectList = [
        { value: '' },
        { value: '' }
      ];
      this.nowOption = [];
      this.nowOptionx = [];

      if (e === 0) {
        this.getGroupTree();
      } else {
        this.getDev();
      }
    },
    getValue(e, index) {
      this.searchSelectList[index].value = e[e.length - 1];
      let obj = {};
      obj = this.nowOptionx.find((item) => {
        return item.id === e[e.length - 1];
      });
      this.detailListName[index] = obj.name;
    },
    getValue2(e, index) {
      this.searchSelectList[index].value = e + '';
      let obj = {};
      obj = this.nowOption.find((item) => {
        return item.id === e;
      });
      this.detailListName[index] = obj.name;
    },
    minusSearch(index) {
      this.searchSelectList.splice(index, 1);
    },
    addSearch() {
      this.searchSelectList.push({ value: null });
    },
    search() {
      this.detailListx = [];
      setTimeout(() => {
        this.detailListx = JSON.parse(JSON.stringify(this.detailListName));
      }, 0);
    },
    reset() {
      const end = new Date();
      const start = new Date();
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
      this.dateTime = [
        this.$utils.toDateString(start, 'yyyy-MM-dd') + ' 00:00:00',
        this.$utils.toDateString(end, 'yyyy-MM-dd') + ' 23:59:59'
      ];

      this.detailListx = [];
      this.detailListName = ['', ''];
      this.typeModel = 0;
      this.nowOption = [];
      this.nowOptionx = [];
      this.searchSelectList = [
        { value: '' },
        { value: '' }
      ];

      this.getGroupTree();
    },
    /**
     * @description: 获取类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await deviceGroupGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOption = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }

        const resx = await deviceGroupGetTreeAction();
        if (resx.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOptionx = resx.rows;
        } else {
          // 提示异常信息
          showWarningMessage(resx.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getDev() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await deviceGetPageDeviceComplexByConditionAction({
          current: 1,
          size: 99999999
        },
        {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // this.nowOption = res.rows;
          this.nowOption = res.rows.map((item) => {
            item.id = item.deviceCode;
            item.name = item.deviceName;
            return item;
          });
          this.nowOptionx = this.nowOption;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }

        const resx = await deviceGroupGetTreeAction();
        if (resx.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nowOptionx = resx.rows;
        } else {
          // 提示异常信息
          showWarningMessage(resx.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  components: {
    // 加载未审核组件
    SearchBox,
    HeaderTitle,
    HeadOperationItem,
    detailTable,
    chartModule
    // paramWarn
    // alarmStatistics,
    // multiDeviceComparison,
    // efficiencyStatistics
  }
};
